* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&family=Quicksand:wght@300&family=Rowdies:wght@300;400&display=swap");
body {
  margin: 0;
  height: 100vh;
  font-family: poppins;
}
.allcontent {
  padding: 0px 50px;
}
.image {
  background-color: #060a0f;
  display: flex;
  justify-content: center;
  border-radius: 16px 16px 0px 0px;
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  border-radius: 16px;
  background-color: #1a1b21;
  color: white;
  margin: 0 auto;
  width: 400px;
}
.content {
  text-align: center;
}
.content h1 {
  padding-top: 21px;
  font-weight: bold;
  font-size: 20px;

  text-align: center;

  color: #ffffff;
}
.content h2 {
  font-weight: normal;
  font-size: 12.8px;
  text-align: center;
  color: #f3bf99;
}
.content p {
  font-style: normal;

  padding-top: 5px;
  font-size: 11px;
}
.buttons {
  margin-top: 15px;
  display: flex;
  justify-content: space-around;

  text-align: center;
}
.buttons btn {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
  text-align: center;
  border-radius: 6px;
  background-color: rgb(255, 255, 255);
  color: black;
  width: 115px;
  height: 34px;
  transition: all 0.14s ease-out;
}
.buttons btn:hover {
  transform: translate(-0.25rem, -0.25rem);
  box-shadow: 4px 4px 0px #f3bf99;
}
.buttons btn:nth-child(2) {
  color: white;
  background-color: #5093e2;
}

.btn21 {
  color: #1d1d1d;
}
.btn22 {
  color: #ffffff;
}
.buttons btn img {
  padding: 0 4px;

  object-fit: none;
}

.about {
  margin-top: 33px;
}
.second-content h1 {
  color: #5093e2;
  font-weight: bold;
  font-size: 20px;
}
.second-content p {
  font-weight: 200;
  font-size: 14.24px;
}
.interests {
  margin-top: 24px;
}
a {
  text-decoration: none;
}
.footer {
  border-radius: 0px 0px 16px 16px;
  margin-top: 24px;
  width: auto;
  height: 64px;
  background: #161619;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
}
.footer a {
  cursor: pointer;
  margin: 0 10px;
  transition: 0.5s linear;
}
.footer a:hover {
  transform: translate(-0.25rem, -0.25rem);
}
@media only screen and (max-width: 460px) {
  body {
    background-color: #1a1b21;
    border-radius: 0;
  }
  .container {
    position: inherit;

    transform: translate(0, 0);
    background-color: #1a1b21;
    color: white;

    width: 100%;
  }
  .image {
    border-radius: 0;
  }
  .about {
    padding-top: 15px;
  }
  .buttons {
    display: flex;
    flex-wrap: wrap;
  }
  .buttons btn {
    margin-top: 15px;
  }
  .allcontent {
    padding-top: 15px;
    padding-bottom: 75px;
  }
  .footer {
    border-radius: 0;
  }
}
